@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1A1A1A;
  @apply text-black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2{
  @apply font-heebo font-bold text-3xl;
}

h3{
  @apply font-heebo font-medium text-5xl;
}

h1{
  @apply font-heebo font-bold lg:text-6xl leading-13 text-4xl;
}

p{
  @apply font-roboto font-normal text-lg text-gray-400;
}